@font-face {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/montserrat-v25-latin_cyrillic-regular.woff2")
            format("woff2"),
        url("../fonts/montserrat-v25-latin_cyrillic-regular.woff")
            format("woff");
}

@font-face {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    src: url("../fonts/montserrat-v25-latin_cyrillic-700.woff2") format("woff2"),
        url("../fonts/montserrat-v25-latin_cyrillic-700.woff") format("woff");
}
